@media (min-width: 500px) {
  .layout--uni-3col {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 40px;
    display: grid;
  }
}

@media (min-width: 1000px) {
  .layout--uni-3col {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 60px;
  }
}

.layout--uni-3col > .region > * {
  margin: 60px 0;
}

@media (max-width: 999.98px) {
  .layout--uni-3col > .region > * {
    margin: 40px 0;
  }
}

@media (min-width: 500px) {
  .layout--uni-3col > .region > :first-of-type {
    margin-top: 0;
  }

  .layout--uni-3col > .region > :last-of-type {
    margin-bottom: 0;
  }
}

